@use 'base/variables' as v;
@use 'base/mixins' as m;

.sortiment {
    margin-bottom: 5rem;
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }
    &__titulo {
        @include m.headingCursiva;
        text-align: left;
        margin-top: 5rem;
    }


    &__grid {
        @include m.grid(1, 2rem);
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop { 
            @include m.grid(3, 2rem);
        }
    }
}

.sortiert {
    position: relative;
    overflow: hidden;

    &__contenido {
        background-color: v.$blanco;
        padding: 2rem;

    }
    // &__mask {
    //     position: relative;
    //     overflow: hidden;
    // }

    &__imagen {
        // width: 100%;
        width: 100%;
        transition-property: transform;
        transition-duration: .5s;
        &:hover {
            transform: scale(1.2);
        }
    }

    &__nombre {
        color: v.$amarillo;
        position: absolute;
        bottom: 0;
        left:0;
        padding: 1.5rem 1rem;
        margin: 0;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        text-align: left;
        text-transform: uppercase;
        transition-duration: .5s;
        
        &:hover {
            bottom: -1rem;
            opacity: 0;
        }
    }

    &__descripcion {
        font-size: 1.6rem;
        text-transform: uppercase;
        line-height: 1.8;
    }

}

