@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: darken(v.$grisOscuro, 10%);;
    padding-top: 3rem;
    

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo {
        width: 15rem;
    }
    &__heading {
        @include m.headingCursiva;
        font-size: 4rem;
    }
    &__texto {
        color: v.$gris;
        max-width: 20rem;
        font-size: 1.8rem;
        margin-top: 0;
        margin-bottom: 1rem;
    }

    &__logo {
        width: 15rem;
    }

    &__navegacion {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: .3rem 0;
        margin: 2rem 0;
    
        @include m.tablet { 
            flex-direction: row;
            gap: 2rem;
            margin-top: 0;
        }

    }
    &__enlace {
        color: v.$blanco;
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 1.2rem;

        &:hover {
            color: v.$amarillo;
        }
        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet { 
            margin-bottom: 0;
        }
        
    }
}

.kundeninfo{ 
    
    margin: 2rem auto 0 auto;
    background-color: darken(v.$grisOscuro, 10%);
    border-top: 1px solid darken(v.$grisClaro, 10%);
    

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content:center;
        }
    }

    &__copyright {
        text-align: center;
        font-size: 1.2rem;
        padding: 1rem;
        color: v.$gris;
    }

    &__navegacion {
        display: flex;
        flex-direction: row;
        gap: 2rem;
        padding-left: 5rem;
        
    }

    &__info {
        font-size: 1.5rem;
        color: v.$gris;
        font-size: 1.5rem;
        font-weight: bold;

        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet { 
            margin-bottom: 0;
        }
        
    }


}

