@use 'base/mixins' as m;
@use 'base/variables' as v;

.iconos {
    padding-top: 0;
    
    &__grid {
        
        @include m.grid(1, 2rem);
        @include m.desktop { 
            @include m.grid(3, 2rem);
        }
    }

    &__heading {
        @include m.headingCursiva;
        margin: 5rem;
    }
    &__nombre {
        padding-top: 2rem;
        @include m.heading;
        font-size: 2.4rem;
        color: v.$rojoclaro;
    }
    &__texto {
        font-size: 2rem;
        line-height: 1.8;
        text-align: center;
    }

    &__icono {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__imagen {
        width: 10rem;
    }
}
