@use 'base/variables' as v;
@use 'base/mixins' as m;


.nosotros {
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }

    &__bienvenida {
        @include m.grid(1, 2rem);

        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        
    }

    &__grid {
        @include m.grid(1, 2rem);

        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop { 
            @include m.grid(3, 2rem);
        }
    }
    &__texto {
        font-size: 2rem;
        line-height: 1.8;
        text-align: center;
    }
}