@use 'base/variables' as v;
@use 'base/mixins' as m;

.cards {

    margin-top: 12rem;
    &__grid {
        @include m.tablet { 
            display: flex;
            justify-content:space-around
        }
    }
}

.card {
    max-width: 35rem;
    margin: 0 auto 2rem auto;

    &:last-of-type {
        margin-bottom: 0;
    }
    @include m.tablet { 
        max-width: unset;
        margin: 0;
    }

    &__imagen {
        width: 100%;
        
        border: .5rem solid v.$blanco;
        transition-property: rotate scale;
        transition-duration: 300ms;

        &:hover {
            transform: rotate(4deg) scale(1.3);
        }
    }

    &__contenido {
        max-width: 90%;
        margin: 0 auto;
        background-color: v.$blanco;
        transform: translateY(-5rem);
    }
    &__heading {
        @include m.headingCursiva;
        font-size: 3.4rem;
        padding: 2rem;
    }
    &__enlace {
        @include m.boton(v.$rojoclaro, v.$blanco);
        display: block;
    }
}