@use 'base/variables' as v;
@use 'base/mixins' as m;

.impressum {
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }

    &__nombre {
        @include m.heading;
        text-align: left;
        font-size: 2rem;
        color: v.$rojoclaro;
    }

    &__texto {
        font-size: 2rem;
        line-height: 1.8;
        padding-bottom: 5rem;
        
    }
}