@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: .3rem 0;
    margin: 2rem 0;


    position: relative;
    z-index: 1;

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; 

    @include m.tablet { 
        flex-direction: row;
        gap: 2rem;
        margin-top: 0;
        opacity: 1;
        visibility: visible;
    }

    &__enlace {
        color: v.$blanco;
        font-size: 2.2rem;
        font-weight: bold;
        margin-bottom: 1.2rem;
        &:hover{
            color: v.$amarillo;
        }

        &:last-of-type {
            margin-bottom: 0;
        }
        @include m.tablet { 
            margin-bottom: 0;
        }
        
    }
}

/** Menu de hamburguesa **/
.mobile-menu {
    @include m.tablet {
        display: none;
    }
    img {
        width: 5rem;
    }
    &:hover {
        cursor: pointer;
        color: v.$amarillo;
    }
}
.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}