@use 'base/variables' as v;
@use 'base/mixins' as m;

.produktebes {

    padding-top: 5rem;
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;
    }

    &__grid {
        @include m.grid(1, 2rem);
        @include m.tablet { 
            @include m.grid(6, 2rem);
        }


    }

}

.artikel {
    background-color:  v.$grisClaro;
    margin-bottom: 2rem;
    

    @include m.tablet { 
        margin-bottom: 0;

        &:nth-child(1) {
            
            grid-column-start: 1;
            grid-column-end: 7;
            display: grid;
            grid-template-columns: 3fr 1fr;

        }
        &:nth-child(1) img {
            height: 43rem;
            width: 100%;
            object-fit: cover;
        }

        &:nth-child(2) {
            grid-column-start: 1;
            grid-column-end: 4;
        }
        &:nth-child(2) img {
            height: 30rem;
            width: 100%;
            object-fit: cover;            
        }

        &:nth-child(3) {
            grid-column-start: 4;
            grid-column-end: 7;
        }
        &:nth-child(3) img {
            height: 30rem;
            width: 100%;
            object-fit: cover;             
        }

        &:nth-child(3) {
            grid-column-start: 4;
            grid-column-end: 7;
        }
        &:nth-child(4) {
            grid-column-start: 1;
            grid-column-end: 3;
        }
        &:nth-child(5) {
            grid-column-start: 3;
            grid-column-end: 5;
        }
        &:nth-child(6) {
            grid-column-start: 5;
            grid-column-end: 7;
        }

  
    }

    &__nombre {
        @include m.heading;
        font-size: 2.2rem;
        color: v.$naranja

    }

    &__contenido {
        text-align: center;
        color: v.$negro;
        padding: 2rem;
    }
    &__texto {
        font-size: 1.6rem;
        text-transform: uppercase;
        line-height: 1.8;
    }
}