@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-image: linear-gradient(to right, rgb( 0 0 0 / .7), rgb(0 0 0 / .7)), url(../img/tapas01.jpg);
    min-height: 30rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position:  center;
    padding: 4rem 0;
    position: relative;
    
        @include m.tablet { 
        padding: 5rem 0 10rem 0;
    }

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo {
        width: 25rem;
    }

    &__flag {
        display: none;

        @include m.desktop { 
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;

            animation-duration: 1s;
            animation-delay: 0s;
            animation-timing-function: ease-in-out;
            animation-name: animarImagen;
        }
    }

    &__contenido{

        @include m.tablet { 
            margin-top: 5rem;
            max-width: 50rem;
        }
    }
    &__heading {
        color: v.$amarillo;
        text-align: center;
        font-weight: bold;
        font-size: 4rem;
        @include m.tablet {
            text-align: left;
        }
    }
    &__texto {
        color: v.$blanco;
        display: none;
        @include m.tablet { 
            display: block;
        }
    }

}