body {
  background-color: #f8f8f8;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}
.btn:hover {
  color: #a18100;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}
.boton-negro:hover {
  color: #a18100;
}

@keyframes animarImagen {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../img/tapas01.jpg);
  min-height: 30rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 4rem 0;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    padding: 5rem 0 10rem 0;
  }
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
.header__flag {
  display: none;
}
@media (min-width: 1024px) {
  .header__flag {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    animation-duration: 1s;
    animation-delay: 0s;
    animation-timing-function: ease-in-out;
    animation-name: animarImagen;
  }
}
@media (min-width: 768px) {
  .header__contenido {
    margin-top: 5rem;
    max-width: 50rem;
  }
}
.header__heading {
  color: #D4AA00;
  text-align: center;
  font-weight: bold;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
  }
}
.header__texto {
  color: #FFFFFF;
  display: none;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.3rem 0;
  margin: 2rem 0;
  position: relative;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2.2rem;
  font-weight: bold;
  margin-bottom: 1.2rem;
}
.navegacion__enlace:hover {
  color: #D4AA00;
}
.navegacion__enlace:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0;
  }
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu:hover {
  cursor: pointer;
  color: #D4AA00;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.slider {
  overflow: hidden;
  position: relative;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.slider__imagen {
  width: 100%;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #FFFFFF;
}
.slider .swiper-pagination-bullet {
  background-color: #FFFFFF;
}
.slider .swiper-pagination-bullet-active {
  background-color: #8ed3cc;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-slide img {
  display: block;
  width: 100%;
}

.bienvenida {
  text-align: center;
}
.bienvenida__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
}
.bienvenida__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 50rem;
  margin: 4rem auto 0 auto;
  line-height: 1.8;
}

.cards {
  margin-top: 12rem;
}
@media (min-width: 768px) {
  .cards__grid {
    display: flex;
    justify-content: space-around;
  }
}

.card {
  max-width: 35rem;
  margin: 0 auto 2rem auto;
}
.card:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}
.card__imagen {
  width: 100%;
  border: 0.5rem solid #FFFFFF;
  transition-property: rotate scale;
  transition-duration: 300ms;
}
.card__imagen:hover {
  transform: rotate(4deg) scale(1.3);
}
.card__contenido {
  max-width: 90%;
  margin: 0 auto;
  background-color: #FFFFFF;
  transform: translateY(-5rem);
}
.card__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  font-size: 3.4rem;
  padding: 2rem;
}
.card__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.card__enlace {
  background-color: #a20202;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
}
@media (min-width: 768px) {
  .card__enlace {
    width: auto;
  }
}
.card__enlace:hover {
  background-color: #700101;
}
.card__enlace:hover {
  color: #a18100;
}

.horario {
  background-image: linear-gradient(to bottom, #a20202 0%, #a20202 50%, transparent 50%), url(../img/bg_horarios.png);
  background-repeat: no-repeat;
  background-position: center, bottom center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .horario {
    background-image: linear-gradient(to right, #a20202 0%, #a20202 50%, transparent 50%), url(../img/bg_horarios.png);
    background-position: center, top right;
  }
}
.horario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__contenido {
  padding: 5rem 0;
}
.horario__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
}
.horario__texto {
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.tabla {
  width: 100%;
  border: 0.5rem solid #D4AA00;
  text-align: center;
  border-collapse: collapse;
}
.tabla__thead {
  background-color: #D4AA00;
}
.tabla__th {
  padding: 2rem;
}
.tabla__tr:hover {
  background-color: #b67401;
}
.tabla__td {
  padding: 1rem;
  font-size: 2rem;
}

.productos__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  margin-bottom: 5rem;
}
.productos__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.productos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.producto__contenido {
  background-color: #FFFFFF;
  padding: 2rem;
}
.producto__mask {
  overflow: hidden;
}
.producto__imagen {
  width: 100%;
  transition-property: transform;
  transition-duration: 300ms;
}
.producto__imagen:hover {
  transform: scale(1.2);
}
.producto__nombre {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 2.2rem;
  color: #a20202;
}
.producto__descripcion {
  font-size: 1.6rem;
  text-transform: uppercase;
  line-height: 1.8;
}

.cita {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../img/bg_alhambra.png);
  min-height: 50rem;
  background-position: bottom center;
  background-size: cover;
  background-attachment: fixed;
  padding: 10rem 0;
  text-align: center;
}
.cita__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  color: #D4AA00;
  font-size: 6rem;
  font-weight: bold;
}
.cita__texto {
  max-width: 60rem;
  margin: 0 auto;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
}

.iconos {
  padding-top: 0;
}
.iconos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .iconos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  margin: 5rem;
}
.iconos__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.iconos__nombre {
  padding-top: 2rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 4rem;
  font-size: 2.4rem;
  color: #a20202;
}
.iconos__texto {
  font-size: 2rem;
  line-height: 1.8;
  text-align: center;
}
.iconos__icono {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.iconos__imagen {
  width: 10rem;
}

.sortiment {
  margin-bottom: 5rem;
}
.sortiment__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  margin-bottom: 5rem;
}
.sortiment__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.sortiment__titulo {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  text-align: left;
  margin-top: 5rem;
}
.sortiment__titulo span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.sortiment__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .sortiment__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .sortiment__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.sortiert {
  position: relative;
  overflow: hidden;
}
.sortiert__contenido {
  background-color: #FFFFFF;
  padding: 2rem;
}
.sortiert__imagen {
  width: 100%;
  transition-property: transform;
  transition-duration: 0.5s;
}
.sortiert__imagen:hover {
  transform: scale(1.2);
}
.sortiert__nombre {
  color: #D4AA00;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 1.5rem 1rem;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  text-align: left;
  text-transform: uppercase;
  transition-duration: 0.5s;
}
.sortiert__nombre:hover {
  bottom: -1rem;
  opacity: 0;
}
.sortiert__descripcion {
  font-size: 1.6rem;
  text-transform: uppercase;
  line-height: 1.8;
}

.anreise__heading {
  text-align: center;
  color: #a20202;
  font-family: "Italianno", sans-serif;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 6rem;
  margin-bottom: 5rem;
}
.anreise__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.anreise__mapa {
  height: 50rem;
}

