@use 'base/variables' as v;
@use 'base/mixins' as m;

.cita {
    background-image: linear-gradient(to right, rgb( 0 0 0 / .5), rgb(0 0 0 / .5)), url(../img/bg_alhambra.png);
    min-height: 50rem;
    background-position: bottom center;
    background-size: cover;
    background-attachment: fixed;
    padding: 10rem 0;
    text-align: center;

    &__heading {
        @include m.heading;
        color: v.$amarillo;
        font-size: 6rem;
        font-weight: bold;
    }

    &__texto {
        max-width: 60rem;
        margin: 0 auto;
        color: v.$blanco;
        font-weight: bold;
        line-height: 2;
    }
}