@use 'base/variables' as v;
@use 'base/mixins' as m;

.horario {
    background-image: linear-gradient( to bottom, v.$rojoclaro 0%, v.$rojoclaro 50%, transparent 50% ), url(../img/bg_horarios.png);
    background-repeat: no-repeat;
    background-position: center, bottom center;
    color: v.$blanco;
    

    @include m.tablet { 
        background-image: linear-gradient( to right, v.$rojoclaro 0%, v.$rojoclaro 50%, transparent 50% ), url(../img/bg_horarios.png);
        background-position: center, top right;
    }


    &__grid {
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        @include m.tablet { 
            grid-template-rows: unset;
            @include m.grid(2, 5rem);
        }
    }
    &__contenido {
        padding: 5rem 0;
    }
    &__heading {
        @include m.heading;
    }
    &__texto {
        font-size: 2.2rem;
        line-height: 1.8;
        text-align: center;
    }
}

.tabla {
    width: 100%;
    border: .5rem solid v.$amarillo;
    text-align: center;
    border-collapse: collapse;
    &__thead {
        background-color: v.$amarillo;
        
    }
    &__th {
        padding: 2rem;
        
    }
    &__tr {
        
        &:hover {
            background-color: darken(v.$naranja, 10%);
        }
    }
    &__td {
        padding: 1rem;
        font-size: 2rem;
    }
}